*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.app{
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  .vid-container{
    position: fixed;
    top: 0%;
    left: 0%;
    z-index: -10;
    video{
      
    }
  }
  .time-select, .sound-picker, .player-container{
    height: 80%;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content:space-evenly;
  }
  .time-select button, .sound-picker button{
    color: white;
    width: 30%;
    height: 10%;
    background: none;
    border: 2px solid white;
    cursor: pointer;
    border-radius: 5px;
    font-size: 20px;
    transition: all 0.5s ease;
  }
  .time-select button:hover{
    color: black;
    background: white;
  }
  .sound-picker button{
    border: none;
    border-radius: 50%;
    height: 120px;
    width: 120px;
    padding: 30px;
    img{
      height: 100%;
    }
  }
  .sound-picker button:nth-child(1){
    background: #4972a1;
  }
  .sound-picker button:nth-child(2){
    background: #a14f41;
  }
  .time-select{

  }
  .sound-picker{
  }
  .player-container{
    position: relative;
    svg{
      position: absolute;
      height: 50%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(-90deg);
      pointer-events: none;
    }
    .time-display{
      position: absolute;
      bottom: 10%;
      color: white;
      font-size: 50px;
      text-align: center;
      width: 100%;
    }
  }
}
